.collection-block__list {
  display: flex;
  align-items: flex-end;
  padding: 0;
  position: relative;

  &::before {
    position: absolute;
    content: "";
    border-bottom: 1px solid @black;
    bottom: 0;
    width: 100%;
  }
}

.collection-block__item {
  list-style: none;
  margin-right: 40px;
  margin-bottom: 10px;
}

.collection-block__link {
  position: relative;
  font-weight: 400;
  font-size: 16px;
  line-height: 25px;
  margin-bottom: 10px;
  text-decoration: none;
  color: @black;
  font-family: @deafult-font;

  @media (max-width: 768px) {
    display: none;
  }
}

.collection-block__heading {
  font-weight: 400;
  font-size: 45px;
  line-height: 50px;
  margin: 0;
  font-family: @deafult-font;

  @media (max-width: 768px) {
    font-size: 30px;
  }
}

.collection-block__swiper {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  z-index: 1;
}

.collection-block__small {
  background-color: @pink--shade;
  position: relative;
}

.collection-block__swiper--item {
  display: flex;
}

.collection-block__bestseller {
  position: absolute;
  background-color: @shade--cyan;
  padding: 15px;
  font-weight: 400;
  font-size: 15px;
  line-height: 25px;
  color: @white;
  top: 0;

  @media (min-width: 991px) {
    padding: 11px 13px 9px 20px;
    margin-top: 8px;
    left: -18px;
  }
}

.collection-block__new {
  position: absolute;
  background-color: @dark--shade--cyan--blue;
  color: @white;
  font-weight: 400;
  font-size: 15px;
  line-height: 25px;
  padding: 15px;
  top: 61px;

  @media (min-width: 991px) {
    padding: 8px 15px 12px 16px;
    top: 53px;
    left: -19px;
  }
}

.collection-block {
  position: relative;
}

section.collection-block {
  @media (min-width: 991px) {
    padding-top: 100px;
  }
}

.collection-block__swiper--list {
  display: flex;
}

.collection-block__product--name {
  font-weight: 400;
  font-size: 16px;
  line-height: 25px;
  list-style: none;
  margin-top: 10px;
  font-family: @deafult-font;
}

.collection-block__product--partnumber {
  font-weight: 400;
  font-size: 16px;
  line-height: 25px;
  color: @medium--light--shade--gray;
  list-style: none;
  font-family: @deafult-font;
}

.collection-block__buying {
  display: flex;
  padding: 0;
  justify-content: space-between;
}

.collection-block__price {
  font-weight: 400;
  font-size: 16px;
  line-height: 25px;
  list-style: none;
  font-family: @deafult-font;
}

.collection-block__button {
  list-style: none;
}

.collection-block__button--link {
  padding: 7px 75px 8px;
  color: @white;
  background: @red--shadow;
  text-decoration: none;
  transition: 0.3s ease;
  font-family: @deafult-font;
  font-weight: 400;
  font-size: 15px;
  line-height: 25px;

  &:hover {
    border: 2px solid @shade--red;
    border-radius: 20px;
  }
}

.collection-block__product {
  padding: 0;
}

.swiper-button-next--collection.collection-block__arrow--next {
  content: url("../img/collectionarrowright.svg");
  position: absolute;
  width: 40px;
  height: 40px;
  right: 0;
  cursor: pointer;
  top: 10px;

  &:hover {
    content: url("../img/collectionarrowrightred.svg");
  }
}

.swiper-button-prev--collection.collection-block__arrow--prev {
  content: url("../img/collectionarrowleft.svg");
  position: absolute;
  right: 53px;
  left: auto;
  width: 40px;
  height: 40px;
  cursor: pointer;
  top: 10px;

  &:hover {
    content: url("../img/collectionarrowleftred.svg");
  }
}

.collection-block__plus--red {
  margin-left: 0;

  @media (max-width: 900px) {
    margin: 0;
  }
}

.collection-block__plus--enter {
  &::after {
    cursor: pointer;
    content: url("../img/blackplus.png");
    position: absolute;
    bottom: 27%;
    right: 82%;
  }

  &:hover::after {
    content: url("../img/blackplusred.png");
  }

  &:active::after {
    content: url("../img/blackplusredactive.png");
  }
}

.collection-block__plus--one {
  @media (max-width: 768px) {
    bottom: 53%;
    right: 75%;
  }

  &::after {
    bottom: 38%;
    right: 87%;

    @media (max-width: 768px) {
      bottom: 53%;
      right: 75%;
    }
  }
}

.collection-block__plus--two {
  &::after {
    bottom: 65%;
    right: 54%;

    @media (max-width: 768px) {
      bottom: 76%;
      right: 79%;
    }
  }
}

.collection-block__plus--three {
  &::after {
    bottom: 33%;
    right: 22%;

    @media (max-width: 768px) {
      bottom: 63%;
      right: 28%;
    }
  }
}

.collection-block__plus--four {
  &::after {
    right: 68%;
    bottom: 47%;

    @media (max-width: 768px) {
      right: 43%;
      bottom: 39%;
    }
  }
}

.collection-block__plus--black {
  position: relative;

  @media (max-width: 991px) {
    width: 100%;
  }
}

.collection-block__position {
  position: relative;

  @media (max-width: 768px) {
    display: none;
  }
}

.collection-block__position--opened {
  @media (max-width: 768px) {
    opacity: 1;
    position: fixed;
  }
}

.collection-block__big {
  @media (max-width: 991px) {
    width: 100%;
  }
}
